import React, { useState } from "react";
import { Link } from "react-router-dom";
import "../styles/myInfo.scss";

let mockData = {
  id: 1,
  name: "",
  classAsStudent: ["", "2"],
  classAsTeacher: [""],
};

function MyInfo() {
  console.log(mockData);
  return (
    <div>
      <div className="Info-nav-bar">
        {mockData.name}   <button></button>
      </div>
      <div className="main-box">
        <div className="teacher">
          <p>  </p>
          {mockData.classAsTeacher.map((x, idx) => {
            return (
              <span className="class">
                <Link
                  to={{
                    pathname: "/teacher-dashboard/" + idx,
                    state: {
                      class: x,
                    },
                  }}
                >
                  {x}
                </Link>
              </span>
            );
          })}
        </div>
        <div className="student">
          <p>  </p>
          {mockData.classAsStudent.map((x, idx) => {
            return (
              <span className="class">
                <Link
                  to={{
                    pathname: "/student-dashboard/" + idx,
                    state: {
                      class: x,
                    },
                  }}
                >
                  {x}
                </Link>
              </span>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default MyInfo;
